<template>
    <v-card>
        <v-toolbar
                flat
                color="blue"
                dark
        >
            <v-spacer></v-spacer>
            <v-btn v-on:click="$emit('submit',sequenceTxt)" icon><v-icon>mdi-check-outline</v-icon></v-btn>
        </v-toolbar>
        <v-textarea
                label="Sequence"
                v-on:change="SequenceChange"
                counter
                full-width
                single-line
        ></v-textarea>
    </v-card>
</template>

<script>
    export default {
        name: "QuerySec",
        data() {
            return {
                sequenceTxt:''
            }
        },
        methods:{
            SequenceChange:function(seq){
               this.sequenceTxt = seq;
            }
        }
    }
</script>

