<template>
  <v-app id="inspire">
    <v-navigation-drawer
            v-model="drawer"
            app
    >
      <v-list dense>
        <v-list-item link>
          <v-list-item-action>
            <v-icon>mdi-home</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Home</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item link>
          <v-list-item-action>
            <v-icon>mdi-contact-mail</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Contact</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar
            app
            color="indigo"
            dark
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title>Alignment</v-toolbar-title>
    </v-app-bar>

    <v-content>
      <v-container
              class="fill-height"
              fluid
      >
        <v-row
                align="center"
                justify="center"
        >
          <v-col class="text-center">
            <QuerySec v-on:submit="onSubmit"></QuerySec>

          </v-col>
        </v-row>
        <v-row><v-spacer></v-spacer></v-row>
        <v-row>
          <v-col class="text-center">
            <ResultSec v-bind:alignEntries="result"></ResultSec>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-footer
            color="indigo"
            app
    >
      <span class="white--text">&copy; 2019</span>
    </v-footer>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-app>
</template>

<script>
  import QuerySec from "@/components/QuerySec";
  import ResultSec from "@/components/ResultSec";
  import Axios from "axios";

  export default {
    components: {
      ResultSec,
      QuerySec
    },
    props: {
      source: String,
    },
    data: () => ({
      drawer: null,
      result:null,
      overlay:false
    }),
  methods:{
    onSubmit:function(sequence){
      this.overlay = true;
      let vm = this;
      Axios.post('/Align', {
        sequence:sequence
      }).then(function(response){
        vm.overlay = false;
        vm.result = response.data;
      }).catch(function(error){
        vm.overlay = false;
        alert(error);
      });
    }
  }
  }
</script>