<template>
    <v-card v-bind:style="{visibility: visibility}">
        <v-toolbar
                flat
                color="blue"
                dark
        >
            <v-spacer></v-spacer>
        </v-toolbar>
        <AlignEntry v-for="(entry,index) in alignEntries" v-bind="entry" v-bind:key="index"></AlignEntry>
    </v-card>
</template>

<script>
    import AlignEntry from "@/components/AlignEntry";
    export default {
        name: "ResultSec",
        components: {AlignEntry},
        data(){
            return {
                visibility: 'hidden'
            }
        },
        beforeUpdate:function(){
                if(this.alignEntries != null){
                    this.visibility = 'visible'
                }
        },
        props:{
            alignEntries:[]
        }
    }
</script>

